.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  max-width: 1024px;
  margin: 0 auto;
  padding: 16px;

  font-size: 14px;
  color: white;
}

a {
  color: white;
}

.App a.next,
.App a.loading {
  text-align: right;

  outline: none;
}

.App .Loading {
  color: white;
}

.App .Loader {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  background: rgba(0, 0, 0);

  display: grid;
  place-items: center center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;

  opacity: 1;
  -webkit-transition: opacity 100s ease;
  -o-transition: opacity 100s ease;
  transition: opacity 100s ease;
}

.App .Loader.hidden {
  opacity: 0;
  pointer-events: none;

  -webkit-transition: opacity 2s ease;

  -o-transition: opacity 2s ease;

  transition: opacity 2s ease;
}

.App .Loader p {
  -webkit-animation: MoveUpDown 2s ease-in-out infinite;
          animation: MoveUpDown 2s ease-in-out infinite;
}

@-webkit-keyframes MoveUpDown {
  0%, 100% {
    -webkit-transform: translate(0, -10px);
            transform: translate(0, -10px);
  }
  50% {
    -webkit-transform: translate(0, 10px);
            transform: translate(0, 10px);
  }
}

@keyframes MoveUpDown {
  0%, 100% {
    -webkit-transform: translate(0, -10px);
            transform: translate(0, -10px);
  }
  50% {
    -webkit-transform: translate(0, 10px);
            transform: translate(0, 10px);
  }
}

.App .Subreddit {
  border: none;
  outline: none;
  color: white;
  background: black;
  font-size: 14px;

  display: inline-block;
  width: auto;
  margin-left: 1px;
}

.App .Subreddit div {
  display: inline;
}

.App .Subreddit br {
  display: none;
}


